.search {
    .form-horizontal {
        display: inline-block;
    }
}
.length-box {
    text-align: right;
    font-size: 12px;
    line-height: 20px;
}
#content-box {
    line-height: 23px;
    font-size: 16px;
    letter-spacing: 2px;
    font-weight: 500;
    font-family: "Microsoft YaHei",微软雅黑,宋体;
    outline: 0;
    padding: 5px;
    border-radius: 5px;
    width: 100%;
    min-height: 500px
}
.book-item {
    background-color: #fff;
    display: grid;
    grid-template-columns: 1fr 80px;
    margin-bottom: 20px;
    padding: 10px;
    .actions {
        text-align: center;
        background-color: #5cb85c;
        color: #fff;
        margin: -10px;
        display: flex;
        justify-content:center;
        align-items:Center;
        a {
            color: #fff;
        }
    }
}

.dialog-progress {
    top: 50vh;
    width: 50%;
    display: none;
    background-color: #fff;
    .dialog-body {
        progress {
            width: 88%;
        }
    }
}